'use client';

import { useEffect, useState } from "react";
import { useUser } from "@clerk/nextjs";

interface GoogleEvent {
  id: string;
  summary: string;
  start: { dateTime?: string; date?: string };
  end: { dateTime?: string; date?: string };
}

interface GoogleCalendar {
  id: string;
  summary: string;
  accessRole: string;
}

const ALLOWED_ROLES = ["administratif", "direction école", "direction collège", "direction lycée", "comptabilité"];

export default function GoogleAgenda() {
  const { isLoaded, user } = useUser();
  const [tokens, setTokens] = useState<any>(null);
  const [calendars, setCalendars] = useState<GoogleCalendar[]>([]);
  const [selectedCalendar, setSelectedCalendar] = useState<string>(""); 
  const [events, setEvents] = useState<GoogleEvent[]>([]);
  const [loading, setLoading] = useState(false);
  function hasAccess() {
    if (!isLoaded || !user) return false;
    const rolesRaw = user.publicMetadata?.role;
    let roles: string[] = [];
    if (Array.isArray(rolesRaw)) roles = rolesRaw;
    else if (typeof rolesRaw === "string") roles = [rolesRaw];
    return roles.some(r => ALLOWED_ROLES.includes(r));
  }
  useEffect(() => {
    if (!hasAccess()) return; // pas d'accès
    async function fetchTokens() {
      try {
        const res = await fetch("/api/google/gettokens");
        if (!res.ok) return;
        const data = await res.json();
        const email = Object.keys(data)[0]; // pour tester avec le premier utilisateur
        if (email) setTokens(data[email]);
      } catch (err) {
        console.error("Erreur récupération tokens :", err);
      }
    }
    fetchTokens();
  }, [isLoaded, user]);
  useEffect(() => {
    if (!tokens || !hasAccess()) return;
    async function fetchCalendars() {
      try {
        const res = await fetch(
          "https://www.googleapis.com/calendar/v3/users/me/calendarList",
          {
            headers: { Authorization: `Bearer ${tokens.accessToken}` },
          }
        );
        const data = await res.json();
        setCalendars(data.items || []);
        if (data.items && data.items.length > 0) {
          setSelectedCalendar(data.items[0].id); // agenda principal par défaut
        }
      } catch (err) {
        console.error("Erreur récupération calendriers :", err);
      }
    }
    fetchCalendars();
  }, [tokens]);

  // 3️⃣ Récupération des événements pour le calendrier sélectionné
  useEffect(() => {
    if (!tokens || !selectedCalendar || !hasAccess()) return;
    setLoading(true);
    async function fetchEvents() {
      try {
        const now = new Date().toISOString();
        const res = await fetch(
          `https://www.googleapis.com/calendar/v3/calendars/${encodeURIComponent(
            selectedCalendar
          )}/events?timeMin=${now}&maxResults=50&orderBy=startTime&singleEvents=true`,
          {
            headers: { Authorization: `Bearer ${tokens.accessToken}` },
          }
        );
        const data = await res.json();
        setEvents(data.items || []);
      } catch (err) {
        console.error("Erreur fetch events :", err);
      } finally {
        setLoading(false);
      }
    }
    fetchEvents();
  }, [tokens, selectedCalendar]);

  // 4️⃣ Génération du calendrier à la main
  function renderCalendar() {
    const today = new Date();
    const startOfWeek = new Date(today);
    startOfWeek.setDate(today.getDate() - today.getDay()); // dimanche de la semaine
    const days = Array.from({ length: 7 }, (_, i) => {
      const d = new Date(startOfWeek);
      d.setDate(startOfWeek.getDate() + i);
      return d;
    });

    return (
      <div className="calendar">
        <div className="calendar-header">
          {days.map(day => (
            <div key={day.toDateString()} className="calendar-day-header">
              {day.toLocaleDateString(undefined, { weekday: 'short', day: 'numeric', month: 'short' })}
            </div>
          ))}
        </div>
        <div className="calendar-body">
          {days.map(day => {
            const dayEvents = events.filter(evt => {
              const eventDate = evt.start.dateTime || evt.start.date;
              return new Date(eventDate!).toDateString() === day.toDateString();
            });
            return (
              <div key={day.toDateString()} className="calendar-day">
                {dayEvents.map(evt => (
                  <div key={evt.id} className="calendar-event">
                    {evt.start.dateTime
                      ? new Date(evt.start.dateTime).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })
                      : "All Day"} - {evt.summary}
                  </div>
                ))}
              </div>
            );
          })}
        </div>
        <style jsx>{`
          .calendar { display: flex; flex-direction: column; gap: 1rem; }
          .calendar-header { display: flex; }
          .calendar-day-header { flex: 1; text-align: center; font-weight: bold; }
          .calendar-body { display: flex; }
          .calendar-day { flex: 1; border: 1px solid #ccc; min-height: 150px; padding: 4px; }
          .calendar-event { background: #f0f8ff; margin: 2px 0; padding: 2px 4px; border-radius: 4px; font-size: 0.9rem; }
        `}</style>
      </div>
    );
  }

  // Si utilisateur sans rôle autorisé, ne rien afficher
  if (!hasAccess()) return null;

  // Si pas de tokens, afficher bouton connexion
  if (!tokens) {
    return (
      <a href="/api/google" className="btn">
        Connecter mon agenda Google
      </a>
    );
  }

  return (
    <div>
      <h2>Mon Agenda Google</h2>
      {calendars.length > 0 && (
        <div>
          <label>Choisir un calendrier : </label>
          <select
            value={selectedCalendar}
            onChange={e => setSelectedCalendar(e.target.value)}
          >
            {calendars.map(cal => (
              <option key={cal.id} value={cal.id}>
                {cal.summary} ({cal.accessRole})
              </option>
            ))}
          </select>
        </div>
      )}

      {loading ? <p>Chargement des événements...</p> :
        events.length === 0 ? <p>Aucun événement à afficher.</p> : renderCalendar()}
    </div>
  );
}
